<template>
  <div class="text-center translation-container" id="app">
    <h2>Vue.js Word Translation</h2>
    <h6>by Constantine Doukas</h6>
    <TranslationForm v-on:formSubmit="translateText"></TranslationForm>
    <TranslationOutput v-text="translatedText"></TranslationOutput>
    <h5 v-if="blankText" class="text-warning">Translation text cannot be blank.</h5>
  </div>
</template>

<script>
import TranslationForm from "./components/TranslationForm";
import TranslationOutput from "./components/TranslationOutput";

export default {
  name: "app",
  components: {
    TranslationForm,
    TranslationOutput
  },
  data: function() {
    return {
      blankText: false,
      translatedText: "",
      language: ""
    };
  },
  methods: {
    translateText: function(text, language) {
      if (text === "") {
        this.blankText = true;
        return;
      }

      this.blankText = false;

      this.$http
        .get(
          "https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180928T203108Z.cdcc38122bdf919f.b5dd9251a4a6a8b68ba232c63e4d2f20d3f44740&lang=" +
            language +
            "&text=" +
            text
        )
        .then(response => {
          // eslint-disable-next-line
          this.translatedText = response.body.text[0];
        });
    }
  }
};
</script>

